import { findMaxNumber, generateRandomNumbers } from '../../src/lib/helpers'
import bubble from '../../src/lib/sorts/bubble'
import SortVisualizer from './components/SortVisualizer'
import selection from '../../src/lib/sorts/selection'
import insertion from '../../src/lib/sorts/insertion'

// todo: allow user input for random numbers
let sortProps = {
	arr: generateRandomNumbers({ n: 15, min: 1, max: 100 }),
}

let bubbleProps = {
	sortProps,
	sortFn: bubble,
	maxNumber: findMaxNumber(sortProps.arr),
	containerSelector: '[data-component-sorts="BubbleSort"]',
}

const bubbleSortViz = new SortVisualizer(bubbleProps)

let selectionProps = {
	sortProps,
	sortFn: selection,
	maxNumber: findMaxNumber(sortProps.arr),
	containerSelector: '[data-component-sorts="SelectionSort"]',
}

const selectionSortViz = new SortVisualizer(selectionProps)

let insertionProps = {
	sortProps,
	sortFn: insertion,
	maxNumber: findMaxNumber(sortProps.arr),
	containerSelector: '[data-component-sorts="InsertionSort"]',
}

const insertionSortViz = new SortVisualizer(insertionProps)
